/* src/App.css */
.App {
  text-align: center;
  padding: 20px;
  background: #FFF0F5;
  color: #333;
  min-height: 100vh;
}

html,
body {
  margin: 0;
  padding: 0;
  overflow: hidden; /* prevent any scrolling */
  height: 100%;
}


header h1 {
  color: #E91E63;
  font-family: 'Great Vibes', cursive;
  font-size: 3rem;
  margin-bottom: 10px;
}

header p {
  font-family: 'Lato', sans-serif;
  font-size: 1.2rem;
}


.valentine-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999; /* Ensure it’s on top of everything */
}

/* The modal box */
.valentine-modal-content {
  background: #fff;
  padding: 20px 30px;
  border-radius: 10px;
  text-align: center;
  max-width: 400px;
  position: relative;
}

.valentine-modal-content h2 {
  color: #E91E63;
  margin-bottom: 20px;
}

/* Container for the buttons */
.buttons-container {
  display: flex;
  flex-wrap: wrap; /* so extra yes buttons wrap around */
  gap: 10px;
  justify-content: center;
}

/* Yes Button (reusing your existing style or define a new one) */
.yes-button {
  background: #E91E63;
  color: #FFF;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
}

.yes-button:hover {
  background: #B71C1C;
}

/* Extra yes buttons can have some fun random position or just appear in the container */
.extra-yes {
  position: relative;
  /* you could do random transformations if you want them to swarm around */
}

/* No button */
.no-button {
  background: #ccc;
  color: #333;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
}

.no-button:hover {
  background: #aaa;
}


.button-group button {
  background: #D32F2F;
  color: #FFF;
  border: none;
  padding: 10px 20px;
  margin: 10px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
  transition: background 0.3s ease;
}

.button-group button:hover {
  background: #B71C1C;
}

.background-hearts {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none; /* So hearts don't interfere with other interactions */
  z-index: 0;         /* Place it behind your main content */
}


.background-puzzle {
  position: absolute; 
  top: 200px; /* 15% of the viewport height from the top, for example */
  left: 0;
  right: 0; /* so it can center horizontally if you do text-align */
  text-align: center; 
  pointer-events: none; 
  z-index: 1;  /* above hearts (z=0), below puzzle (z>1) */
  
}

/* Style the puzzle image to be responsive */
.background-puzzle-img {
  max-width: 45vw;  /* up to 50% of the viewport width */
  height: auto;     /* keep aspect ratio */
  opacity: 0.2;       /* or lower if you want it faint behind the puzzle */
  /* If you want some minimum or maximum size, add: */
  min-width: 300px;
  max-width: 600px; 
}

.romantic-image {
  max-width: 45vw;
  height: auto;
  min-width: 300px;
  max-width: 600px;
  border-radius: 10px; /* optional, for a nice style */
}

.romantic-image-container {
  position: absolute;
  top: 200px;
  left: 0;
  right: 0;
  text-align: center;
  z-index: 2; /* above the background puzzle (z=1) */
  pointer-events: none; /* if you don’t want it to intercept clicks */
}

.romantic-image {
  max-width: 45vw;
  min-width: 300px;
  max-width: 600px;
  height: auto;
  border-radius: 10px;
}


@media (max-width: 600px) {
  .background-puzzle-img {
    max-width: 80vw;
    min-width: 200px; 
  }

  .background-puzzle {
    top: 10vh; 
  }
}

/* Style for each heart */
.heart {
  position: absolute;
  color: rgba(211, 47, 47, 0.1); /* A subtle red (using primary red with low opacity) */
  opacity: 0;
  animation: fadeInOut 6s ease-in infinite;
}

/* Keyframes for fade in/out animation */
@keyframes fadeInOut {
  0% {
    opacity: 0.1;
    transform: scale(0.5);
  }
  50% {
    opacity: 0.5;
    transform: scale(1);
  }
  100% {
    opacity: 0.1;
    transform: scale(0.5);
  }
}



.love-page {
  position: relative;
  text-align: center;
  color: #333;
  padding: 20px;
}

.love-page header h1 {
  color: #E91E63;
  font-family: 'Great Vibes', cursive;
  font-size: 3rem;
  margin-bottom: 10px;
}

.love-page header p {
  font-family: 'Lato', sans-serif;
  font-size: 1.2rem;
  margin-bottom: 20px;
}

.love-image-container img {
  max-width: 400px; /* adjust as needed */
  border-radius: 10px;
  margin-bottom: 20px;
}

.love-message {
  font-size: 1.1rem;
  margin: 0 auto;
  max-width: 600px;
  line-height: 1.5;
  color: #444;
  top: 1050px;
}




